

import {
  AfterContentInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChildren,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  QueryList,
  SimpleChanges,
  TemplateRef,
  ViewEncapsulation
} from '@angular/core';
import { merge, Subject, Subscription } from 'rxjs';
import { startWith, takeUntil } from 'rxjs/operators';

import { Direction, Directionality } from '@angular/cdk/bidi';
import { TDSStepComponent } from './step.component';
import { BooleanInput } from '@angular/cdk/coercion';
import { ButtonSize, NgClassType } from "tds-ui/core/config";
import { toBoolean } from 'tds-ui/shared/utility';

export type TDSDirectionType = 'horizontal' | 'vertical';
export type TDSStatusType = 'wait' | 'process' | 'finish' | 'error';
export type TDSProgressDotTemplate = TemplateRef<{ $implicit: TemplateRef<void>; status: string; index: number }>;
@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  preserveWhitespaces: false,
  selector: 'tds-steps',
  exportAs: 'tdsSteps',
  template: `
    <ng-content></ng-content>
  `,
  host: {
    '[class]': "classMap",
  }
})
export class TDSStepsComponent implements OnChanges, OnInit, OnDestroy, AfterContentInit {
  static ngAcceptInputType_progressDot: BooleanInput | TDSProgressDotTemplate | undefined | null;

  @ContentChildren(TDSStepComponent) steps!: QueryList<TDSStepComponent>;
  @Input() mode: 'default' | 'bar' = 'default';
  @Input() current = 0;
  @Input() direction: TDSDirectionType = 'horizontal';
  @Input() labelPlacement: 'horizontal' | 'vertical' = 'horizontal';
  @Input() type: 'default' | 'navigation' = 'default';
  @Input() size: ButtonSize = 'md';
  @Input() startIndex = 0;
  @Input() status: TDSStatusType = 'process';
  @Input() showStep: boolean = false;
  @Input() borderStyle: string = 'border-solid';
  @Input() tdsClss: string = 'bg-white dark:bg-d-neutral-3-50';
  @Input()
  set progressDot(value: boolean | TDSProgressDotTemplate) {
    if (value instanceof TemplateRef) {
      this.showProcessDot = true;
      this.customProcessDotTemplate = value;
    } else {
      this.showProcessDot = toBoolean(value);
    }
    this.updateChildrenSteps();
  }

  @Output() readonly indexChange = new EventEmitter<number>();

  private destroy$ = new Subject<void>();
  private indexChangeSubscription?: Subscription;

  showProcessDot = false;
  customProcessDotTemplate?: TemplateRef<{ $implicit: TemplateRef<void>; status: string; index: number }>;
  classMap: NgClassType = {};
  dir: Direction = 'ltr';

  constructor(private cdr: ChangeDetectorRef, @Optional() private directionality: Directionality, private elementRef: ElementRef) {
    this.setClassMap();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.startIndex || changes.direction || changes.status || changes.current || changes.size || changes.labelPlacement || changes.showStep) {
      this.updateChildrenSteps();
    }
    if (changes.progressDot || changes.direction) {

      this.setClassMap();
    }
  }

  ngOnInit(): void {
    this.directionality.change?.pipe(takeUntil(this.destroy$)).subscribe((direction: Direction) => {
      this.dir = direction;
      this.setClassMap();
      this.cdr.detectChanges();
    });

    this.dir = this.directionality.value;
    this.setClassMap();
    this.updateChildrenSteps();
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
    if (this.indexChangeSubscription) {
      this.indexChangeSubscription.unsubscribe();
    }
  }

  ngAfterContentInit(): void {
    if (this.steps) {
      this.steps.changes.pipe(startWith(null), takeUntil(this.destroy$)).subscribe(() => {
        this.updateChildrenSteps();
      });
    }
  }

  private updateChildrenSteps(): void {
    if (this.steps) {
      const length = this.steps.length;
      this.steps.toArray().forEach((step, index) => {
        Promise.resolve().then(() => {
          step.outStatus = this.status;
          step.showProcessDot = this.showProcessDot;
          if (this.customProcessDotTemplate) {
            step.customProcessTemplate = this.customProcessDotTemplate;
          }
          step.clickable = this.indexChange.observers.length > 0;
          step.direction = this.direction;
          step.index = index + this.startIndex;
          step.currentIndex = this.current;
          step.last = length === index + 1;
          step.size = this.size;
          step.labelPlacement = this.labelPlacement;
          step.showStep = this.showStep;
          step.mode = this.mode;
          step.first = index == 0;
          step.borderStyle = this.borderStyle;
          step.backgroundColor = this.tdsClss;
          step.markForCheck();
        });
      });
      if (this.indexChangeSubscription) {
        this.indexChangeSubscription.unsubscribe();
      }
      this.indexChangeSubscription = merge(...this.steps.map(step => step.click$)).subscribe(index => this.indexChange.emit(index));
    }
  }

  private setClassMap(): void {
    this.classMap = {
      [`tds-steps`]: true,
      [`flex`]: true,
      [`w-full`]: true,
      [`tds-steps-${this.direction}`]: true,
      [`flex-col`]: this.direction === 'vertical',
      [`h-full`]: this.direction === 'vertical',
      [`tds-steps-label-horizontal`]: this.direction === 'horizontal',
      [`tds-steps-label-vertical`]: (this.showProcessDot || this.labelPlacement === 'vertical') && this.direction === 'horizontal',
      [`tds-steps-dot`]: this.showProcessDot,
      ['tds-steps-navigation']: this.type === 'navigation',
      ['tds-steps-rtl']: this.dir === 'rtl',
      [`tds-steps-bar`]: this.mode === 'bar',
      [`tds-steps-sm`]: this.size === 'sm',
      [`tds-steps-md`]: this.size === 'md',
      // [`tds-steps-xl`]: this.size === 'xl',
      [`tds-steps-lg`]: this.size === 'lg',
    };
  }
}
