

import { BooleanInput } from '@angular/cdk/coercion';
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  Input,
  OnDestroy,
  TemplateRef,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';

import { Subject } from 'rxjs';
import { ButtonSize, NgClassType } from "tds-ui/core/config";
import { InputBoolean, TDSHelperString } from 'tds-ui/shared/utility';

@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  selector: 'tds-step',
  exportAs: 'tdsStep',
  preserveWhitespaces: false,
  templateUrl: "./step.component.html",
  host: {
    class: 'tds-steps-item overflow-visible relative inline-block  align-top last:flex-none',
    '[class.flex-1]': 'last !== true && mode==="default"',
    '[class.tds-steps-item-wait]': 'status === "wait"',
    '[class.tds-steps-item-process]': 'status === "process"',
    '[class.tds-steps-item-finish]': 'status === "finish"',
    '[class.tds-steps-item-error]': 'status === "error"',
    '[class.tds-steps-item-active]': 'currentIndex === index',
    '[class.tds-steps-item-disabled]': 'disabled',
    '[class.tds-steps-item-custom]': '!!icon',
    '[class.tds-steps-next-error]': '(outStatus === "error") && (currentIndex === index + 1)',
    // '[class.-ml-2.5]': 'mode==="bar"',

  }
})
export class TDSStepComponent implements OnDestroy {
  static ngAcceptInputType_disabled: BooleanInput;

  @ViewChild('processDotTemplate', { static: false }) processDotTemplate?: TemplateRef<void>;

  @Input() tdsTitle?: string | TemplateRef<void>;
  @Input() subtitle?: string | TemplateRef<void>;
  @Input() description?: string | TemplateRef<void>;
  @Input() @InputBoolean() disabled = false;

  @Input()
  get status(): string {
    return this._status;
  }

  set status(status: string) {
    this._status = status;
    this.isCustomStatus = true;
  }

  isCustomStatus = false;
  private _status = 'wait';

  @Input()
  get icon(): NgClassType | TemplateRef<void> | undefined {
    return this._icon;
  }

  set icon(value: NgClassType | TemplateRef<void> | undefined) {
    if (!(value instanceof TemplateRef)) {
      this.oldAPIIcon = typeof value === 'string' && value.indexOf('anticon') > -1;
    } else {
    }
    this._icon = value;
  }

  oldAPIIcon = true;
  private _icon?: NgClassType | TemplateRef<void>;

  customProcessTemplate?: TemplateRef<{ $implicit: TemplateRef<void>; status: string; index: number }>; // Set by parent.
  mode: 'default' | 'bar' = 'default';
  direction = 'horizontal';
  first = false;
  index = 0;
  last = false;
  outStatus = 'process';
  showProcessDot = false;
  clickable = false;
  showStep = false;
  click$ = new Subject<number>();
  classMapTail: NgClassType = {};
  borderStyle: string = 'border-solid';
  backgroundColor: string = "";
  private _size: ButtonSize = "md";
  get size() {
    return this._size;
  }
  set size(size: ButtonSize) {
    this._size = size;

  }
  private _labelPlacement: 'horizontal' | 'vertical' = 'horizontal';
  get labelPlacement() {
    return this._labelPlacement;
  }
  set labelPlacement(labelPlacement: 'horizontal' | 'vertical') {
    this._labelPlacement = labelPlacement
  }
  get currentIndex(): number {
    return this._currentIndex;
  }

  set currentIndex(current: number) {
    this._currentIndex = current;
    if (!this.isCustomStatus) {
      this._status = current > this.index ? 'finish' : current === this.index ? this.outStatus || '' : 'wait';
    }
  }

  private _currentIndex = 0;

  get activeBar() {
    return (this.currentIndex === this.index)
  }
  constructor(private cdr: ChangeDetectorRef) { }

  onClick(): void {
    if (this.clickable && this.currentIndex !== this.index && !this.disabled) {
      this.click$.next(this.index);
    }
  }

  enable(): void {
    this.disabled = false;
    this.cdr.markForCheck();
  }

  disable(): void {
    this.disabled = true;
    this.cdr.markForCheck();
  }

  markForCheck(): void {

    this.cdr.markForCheck();
  }

  ngOnDestroy(): void {
    this.click$.complete();
  }
  get onlyTitle() {
    return !TDSHelperString.hasValueString(this.subtitle) && !TDSHelperString.hasValueString(this.description)
  }
  
}
